import React, { useCallback } from 'react';
import { withRouter } from "react-router-dom";
import {connect} from "react-redux";
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';
import { getClients, setClient } from "../../../stores/block-reducer";
import Box from '@material-ui/core/Box';
import { compose } from "redux";
import throttle from 'lodash.throttle'
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  autocompleteWrapper: {
    width: "33%",
    [theme.breakpoints.down('sm')]: {
      width: "100%",
    },
  },
  content: {
    padding: 32,
    [theme.breakpoints.down('sm')]: {
      padding: 12,
    },
  },
}));

const ClientBlock = ({getClients, clients, client, setClient}) => {

  const onFindName = async (value) => {
    if (value.length > 2) {
      await getClients(value)
    } else if (value === '') {
      await getClients('')
    }
  };
  const classes = useStyles();

  const delayedCallback = useCallback(
    throttle((value) => onFindName(value), 2000),
    [],
  );

  return (
    <Box className={classes.content}>
      <Box p={2} bgcolor="white" color="black">
        <Box p={1} pl={0} fontSize={20} fontWeight={700}>
          Клієнт
        </Box>
        <Box p={1} pl={0} className={classes.autocompleteWrapper}>
          <Autocomplete
            id="size-small-standard"
            size="small"
            options={clients}
            getOptionLabel={(option) => option.label}
            value={client}
            onChange={setClient}
            onInputChange={(e, value) => delayedCallback(value)}
            renderInput={(params) => (
              <TextField {...params} variant="standard" label="І'мя/Прізвище" placeholder="Введіть або виберіть зі списку" />
            )}
          />
        </Box>
        {client && client.name && (<Box pt={2} fontSize={14}>
          <Box p={0.5} pl={0} fontWeight={600}>
            {client.name}
          </Box>
          <Box p={0.5} pl={0}>
            {client.phone}
          </Box>
        </Box>)}
      </Box>
    </Box>
    );
}

let mapStateToProps = ({ blockReducer }) => {
  return {
    clients: blockReducer.clients,
    client: blockReducer.client,
  }
}
export default compose(
  connect(mapStateToProps, { getClients, setClient }),
  withRouter,
)(ClientBlock)