import React, { useEffect } from 'react';
import { withRouter } from "react-router-dom";
import {connect} from "react-redux";
import { getClients, onSubmitForm } from "../../stores/block-reducer";
import Box from '@material-ui/core/Box';
import ClientBlock from './client-block';
import ApplicantsBlock from './applicants-block';
import { compose } from "redux";
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles(() => ({
  button: {
    backgroundColor: 'white',
  },
}));

const ClientBlocks = ({ getClients, clients, submittedData, onSubmitForm }) => {

  useEffect(() => {
    if(clients.length === 0){
      getClients('')
    }
  }, []);

  const classes = useStyles();
  const submitForm = () => {
    onSubmitForm()
  }

  return (
    <Box>
      <ClientBlock/>
      <ApplicantsBlock/>
      {submittedData && submittedData.clientId && (<pre>{JSON.stringify(submittedData, null, 2)}</pre>)}
      <Box display="flex" justifyContent="flex-end" pt={2} pr={4} pb={5}>
        <div style={{background: 'white', borderRadius: 4}}>
          <Button
            variant="outlined"
            color="primary"
            className={classes.button}
            onClick={submitForm}
            disableRipple
          >
            Зберегти
          </Button>
        </div>
      </Box>
    </Box>
    );
}

let mapStateToProps = ({ blockReducer }) => {
  return {
    clients: blockReducer.clients,
    submittedData: blockReducer.submittedData,
  }
}
export default compose(
  connect(mapStateToProps, { getClients, onSubmitForm }),
  withRouter,
)(ClientBlocks)