import React, { useState } from 'react';
import { withRouter } from "react-router-dom";
import Button from '@material-ui/core/Button';
import {connect} from "react-redux";
import FormGroup from '@material-ui/core/FormGroup';
import AddApplicant from './add-applicant';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CloseIcon from '@material-ui/icons/Close';
import { onChangeApplicant, onDeleteNewApplicant } from "../../../stores/block-reducer";
import Box from '@material-ui/core/Box';
import AddIcon from '@material-ui/icons/Add';
import { makeStyles } from '@material-ui/core/styles';
import { compose } from "redux";

const useStyles = makeStyles((theme) => ({
  button: {
    textTransform: 'none'
  },
  checkboxWrapper: {
    display: 'flex',
    alignItems: 'center',
    cursor: 'pointer',
  },
  content: {
    padding: 32,
    [theme.breakpoints.down('sm')]: {
      padding: 12,
    },
  },
}));

const ApplicantsBlock = ({ applicants, onChangeApplicant, onDeleteNewApplicant }) => {
  const [visibleAddApplicant, setVisibleAddApplicant] = useState(false);
  const onChangeCheck = (index, value) => {
    onChangeApplicant(index, value);
  }
  const classes = useStyles();

  const onDelete = (index) => {
    onDeleteNewApplicant(index)
  }

  return (
    <Box className={classes.content}>
      <Box p={2} bgcolor="white" color="black">
        <Box p={1} pl={0} fontSize={20} fontWeight={700}>
          Заявники
        </Box>
        {applicants && applicants.length > 0 && (
          <FormGroup>
            {applicants.map((item, index) => {
              return (
                <Box p={1} pl={0}  key={item.id} className={classes.checkboxWrapper} >
                  <FormControlLabel
                    disabled={item.newApplicant}
                    control={<Checkbox checked={item.checked} onChange={() => onChangeCheck(index, {...item, checked: !item.checked})} color="primary" />}
                    label={<Box display="flex" flexWrap="wrap">
                      <Box fontWeight={600} pr={1}>
                        {item.phone || item.email ?  `${item.name},`: `${item.name}`}
                      </Box>
                      <Box>
                        {item.phone ? item.email ? `${item.phone}, ` : `${item.phone}`: '' }{item.email}
                      </Box>
                    </Box>}
                  />
                  {item.newApplicant && (<Box onClick={() => onDelete(index)}>
                    <CloseIcon color="disabled"/>
                  </Box>)}
                </Box>
                )
            })
            }
          </FormGroup>
        )}
        <Button
          variant="outlined"
          color="primary"
          onClick={() => setVisibleAddApplicant(true)}
          className={classes.button}
          endIcon={<AddIcon/>}
          disableRipple
        >
          Додати
        </Button>
        {visibleAddApplicant && <AddApplicant />}
      </Box>
    </Box>
    );
}

let mapStateToProps = ({ blockReducer }) => {
  return {
    applicants: blockReducer.applicants,
  }
}
export default compose(
  connect(mapStateToProps, { onChangeApplicant, onDeleteNewApplicant }),
  withRouter,
)(ApplicantsBlock)