import { API } from "../../api/index";

const SET_CLIENT = 'SET_CLIENT';
const SET_CLIENTS = 'SET_CLIENTS';
const SET_APPLICANTS = 'SET_APPLICANTS';
const CHANGE_APPLICANT = 'CHANGE_APPLICANT';
const CHANGE_NEW_APPLICANT = 'CHANGE_NEW_APPLICANT';
const SET_COUNTRIES = 'SET_COUNTRIES';
const DELETE_NEW_APPLICANT = 'DELETE_NEW_APPLICANT';
const SUBMIT_FORM = 'SUBMIT_FORM';

const initNewApplicant = {
  legalEntity: false,
  country: {country_code: "ua", country_name: "Ukraine"},
  usreou: '',
  name: ''
}

let initialState = {
  clients: [],
  client: null,
  applicants: [],
  newApplicant: initNewApplicant,
  countriesList: [],
  submittedData: {},
};

const usersReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_CLIENTS:
      return {
        ...state,
        clients: [...action.clients],
      };
    case SET_CLIENT:
      return {
        ...state,
        client: action.client
      }
    case SET_APPLICANTS:
      return {
        ...state,
        applicants: [...action.applicants],
      }
    case CHANGE_APPLICANT:
      const newApplicants = [...state.applicants]
      newApplicants[action.index] = action.value
      return {
        ...state,
        applicants: [...newApplicants],
      }
    case CHANGE_NEW_APPLICANT:
      return {
        ...state,
        newApplicant: action.value,
      }
    case SET_COUNTRIES:
      return {
        ...state,
        countriesList: action.value,
      }
    case DELETE_NEW_APPLICANT:
      return {
        ...state,
        applicants: state.applicants.filter((item, index) => index !== action.index ),
      }
    case SUBMIT_FORM:
      return {
        ...state,
        submittedData: {
          clientId: state.client && state.client.id,
          applicantsIds: state.applicants.filter((item) => item.checked && !item.newApplicant).map(i => i.id),
          newApplicants: state.applicants.filter((item) => item.newApplicant),
        },
      }
    default:
      return state;
  }
}

const setClients = (clients) => ({type: SET_CLIENTS, clients});
const setClientToState = (client) => ({type: SET_CLIENT, client});
const setApplicants = (applicants) => ({type: SET_APPLICANTS, applicants});
const changeApplicant = (index, value) => ({type: CHANGE_APPLICANT, index, value});
const changeNewApplicant = (value) => ({type: CHANGE_NEW_APPLICANT, value});
const setCountries = (value) => ({type: SET_COUNTRIES, value});
const deleteNewApplicant = (index) => ({type: DELETE_NEW_APPLICANT, index});
const submitForm = () => ({type: SUBMIT_FORM});

export const getClients = (clientName) => async (dispatch) => {
  const { items } = await API.getClient(clientName);
  dispatch(setClients(items));
}

export const setClient = (e, value) => async (dispatch) => {
  dispatch(setClientToState(value));
  if(value && value.id){
    const { items } = await API.getApplicants(value.id);
    dispatch(setApplicants(items.map((i) => ({...i, checked: false}))));
  } else {
    dispatch(setApplicants([]));
  }
}

export const onChangeApplicant = (index, value) => (dispatch) => {
  dispatch(changeApplicant(index, value));
}

export const onDeleteNewApplicant = (index) => (dispatch) => {
  dispatch(deleteNewApplicant(index));
}

export const onChangeNewApplicant = (value) => (dispatch) => {
  if(!value){
    dispatch(changeNewApplicant(initNewApplicant));
  } else {
    dispatch(changeNewApplicant(value));
  }
}
export const getCountries = () => async (dispatch) => {
  const data = await API.getCountries();
  dispatch(setCountries(data));
}

export const onSubmitForm = () => (dispatch) => {
  dispatch(submitForm());
}



export default usersReducer;