import React, { useEffect } from 'react';
import { withRouter } from "react-router-dom";
import Button from '@material-ui/core/Button';
import { connect } from "react-redux";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import { getCountries, onChangeNewApplicant, onChangeApplicant } from "../../../../stores/block-reducer";
import Box from '@material-ui/core/Box';
import CustomInput from './custom-input';
import InputAdornment from '@material-ui/core/InputAdornment';
import { makeStyles } from '@material-ui/core/styles';
import SearchIcon from '@material-ui/icons/Search';
import { compose } from "redux";
import Autocomplete from '@material-ui/lab/Autocomplete';
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles((theme) => ({
  button: {
    textTransform: 'none',
    marginTop: 20,
  },
  autocompleteWrapper: {
    width: "33%",
    [theme.breakpoints.down('sm')]: {
      width: "100%",
    },
  },
  input: {
    width: '49%',
    marginTop: 20,
    marginBottom: 20,
    marginRight: '1%',
    [theme.breakpoints.down('sm')]: {
      width: "100%",
      marginRight: 0
    },
  },
  inputsLeft: {
    width: '98%',
    marginTop: 10,
    marginBottom: 10,
    marginRight: '2%',
    [theme.breakpoints.down('sm')]: {
      width: "100%",
      marginRight: 0
    },
  },
  inputsRight: {
    width: '98%',
    marginTop: 10,
    marginBottom: 10,
    marginLeft: '2%',
    [theme.breakpoints.down('sm')]: {
      width: "100%",
      marginLeft: 0
    },
  },
  inputsBlockWrapper: {
    width: '50%',
    [theme.breakpoints.down('sm')]: {
      width: "100%",
    },
  },
  inputsWrapper: {
    display: 'flex',
    [theme.breakpoints.down('sm')]: {
      display: 'block',
    },
  }
}));

const AddApplicant = ({ newApplicant, applicants, onChangeApplicant, onChangeNewApplicant, getCountries, countriesList }) => {
  useEffect(() => {
    if(countriesList.length < 1){
      getCountries();
    }
  }, [])

  const changeNewApplicant = (key, value) => {
    onChangeNewApplicant({ ...newApplicant, [key]: value });
  }

  const setNewApplicant = () => {
    onChangeApplicant(applicants.length, { ...newApplicant, newApplicant: true, checked: true });
    onChangeNewApplicant();
  }

  const classes = useStyles();

  const leftInputs = [
    {key: 'name', label: `І'мя`},
    {key: 'address', label: 'Адреса'},
    {key: 'city', label: 'Місто'},
    {key: 'code', label: 'Індекс'},
  ];

  const rightInputs = [
    {key: 'nameInOriginLanguage', label: `Ім'я мовою походження`},
    {key: 'addressInOriginLanguage', label: 'Адреса мовою походження'},
    {key: 'cityInOriginLanguage', label: 'Місто мовою походження'},
    {key: 'codeInOriginLanguage', label: 'Індекс мовою походження'},
  ];
  return (
      <Box pt={2}>
        <Box p={1} pl={0} color="#c7c7c7" fontWeight={700}>
          Додати заявника
        </Box>
        <Box>
          <FormControl component="fieldset">
            <RadioGroup
              aria-label="gender"
              name="gender1"
              value={!!newApplicant.legalEntity}
              color="primary"
              onChange={() => changeNewApplicant('legalEntity', !newApplicant.legalEntity)}>
              <FormControlLabel value={false} control={<Radio color="primary" />} label="Фізична особа" />
              <FormControlLabel value={true} control={<Radio color="primary" />} label="Юридична особа" />
            </RadioGroup>
          </FormControl>
        </Box>
        <Box>
            <Box p={1} pl={0} className={classes.autocompleteWrapper}>
              <Autocomplete
                id="size-small-standard"
                size="small"
                options={countriesList}
                getOptionLabel={(option) => option.country_name}
                value={newApplicant.country}
                onChange={(e, value) => changeNewApplicant('country', value)}
                renderInput={(params) => (
                  <TextField {...params} variant="standard" label="Країна" placeholder="Введіть або виберіть зі списку" />
                )}
              />
            </Box>
            <FormControl className={classes.input}>
              <TextField
                id="input-with-icon-textfield"
                label="ЕГРПОУ"
                value={newApplicant.usreou}
                onChange={(e) => changeNewApplicant('usreou', e.target.value)}
                InputProps={{
                  endAdornment: (
                    <InputAdornment position="start">
                      <SearchIcon />
                    </InputAdornment>
                  ),
                }}
              />
            </FormControl>
        </Box>
        <Box className={classes.inputsWrapper}>
          <Box className={classes.inputsBlockWrapper}>
            {leftInputs.map((item, index) => {
              return (
                <CustomInput
                  key={item.label}
                  className={classes.inputsLeft}
                  onChange={(e) => changeNewApplicant(item.key, e.target.value)}
                  value={newApplicant[item.key] || ''}
                  label={item.label}
                />
              )
            })}
          </Box>
          {newApplicant.country && newApplicant.country.country_code !== 'ua' && (<Box  className={classes.inputsBlockWrapper}>
            {rightInputs.map((item, index) => {
              return (
                <CustomInput
                  key={item.label}
                  className={classes.inputsRight}
                  onChange={(e) => changeNewApplicant(item.key, e.target.value)}
                  value={newApplicant[item.key] || ''}
                  label={item.label}
                />
              )
            })}
          </Box>)
          }
        </Box>
        <Button
          variant="outlined"
          color="primary"
          className={classes.button}
          onClick={setNewApplicant}
          disableRipple
        >
          Додати
        </Button>
      </Box>
  );
}

let mapStateToProps = ({ blockReducer }) => {
  return {
    newApplicant: blockReducer.newApplicant,
    applicants: blockReducer.applicants,
    countriesList: blockReducer.countriesList,
  }
}
export default compose(
  connect(mapStateToProps, { onChangeNewApplicant, getCountries, onChangeApplicant }),
  withRouter,
)(AddApplicant)